import axios from 'axios'

export const queryData = (method, url) => {
  return (params = {}) => new Promise((resolve, reject) => {
    if (['get', 'delete'].indexOf(method.toLowerCase()) > -1) {
      params = { params }
    }
    return axios[method](url, params).then(({data}) => {
      resolve(data)
    }).catch(() => {
      reject('通讯异常')
    })
  })
}



