import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/news'
    },
    {
      path: '/news',
      name: 'news',
      component: require('views/news.vue')
    },
    {
      path: '/about',
      name: 'about',
      component: require('views/about.vue')
    },
    {
      path: '/login',
      name: 'login',
      component: require('views/login.vue')
    },
    {
      path: '/modify',
      name: 'modify',
      component: require('views/modify.vue')
    },
    {
      path: '/members',
      name: 'members',
      component: require('views/members.vue')
    }
  ]
})



