<template>
  <div class="weui-loadmore">
    <i v-show="showLoading" class="weui-loading"></i>
    <span class="weui-loadmore__tips"><slot>正在加载</slot></span>
</div>
</template>

<script>
export default {
  props: {
    showLoading: false
  }
}
</script>


<style scoped>
  .weui-loadmore{margin:8px 14px;font-size:12px;color:#888;text-align:center;}
  .weui-loading{width:20px;height:20px;display:inline-block;vertical-align:middle;-webkit-animation:weuiLoading 1s steps(12,end) infinite;animation:weuiLoading 1s steps(12,end) infinite;background:transparent url(data:image/svg+xml;base64,) no-repeat;background-size:100%;}
  .weui-loadmore__tips{display:inline-block;vertical-align:middle;}
  @keyframes weuiLoading{
      0%{-webkit-transform:rotate3d(0,0,1,0deg);transform:rotate3d(0,0,1,0deg);}
      100%{-webkit-transform:rotate3d(0,0,1,360deg);transform:rotate3d(0,0,1,360deg);}
  }
</style>



