<template>
    <xa-tab-bar ripple v-show="isShowFooter" :icon-size="24" :items="items"></xa-tab-bar>
</template>

<script>
const items = [
  {link: '/news', text: '公告', default: 'icon-remind_fill'},
  {link: '/members', text: '会员', default: 'icon-group_fill'},
  {link: '/about', text: '我的', default: 'icon-people_fill'}
]
export default {
  data () {
    return {
      items,
      isShowFooter: items.find(item => this.$route.fullPath === item.link)
    }
  },
  watch: {
    '$route.path' () {
      this.isShowFooter = items.find(item => this.$route.fullPath === item.link)
    }
  }
}
</script>




