<template>
  <div class="app-circel-icon xa-webkit-center" :style="computedStyle">
    <i v-if="icon" :class="['iconfont', icon]"></i>
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    size: {
      type: Number,
      default: 40
    },
    color: {
      default: '#f2f2f2'
    },
    icon: String
  },
  computed: {
    computedStyle () {
      return {
        width: this.size + 'px',
        height: this.size + 'px',
        backgroundColor: this.color
      }
    }
  }
}
</script>

<style lang="scss">
  .app-circel-icon {
    border-radius: 50%;
    text-align: center;
    font-size: 18px;
    color: white;
    &>.iconfont{
      font-size: 24px;
    }
  }
</style>



