<template>
  <div class="xa-webkit-vertical xa-flex">
    <div class="xa-flex">
      <xa-input v-model="password" label="原密码" position="top" type="text" placeholder="请输入原密码"></xa-input>
      <xa-input v-model="new_password" label="新密码" position="top" type="text" placeholder="请输入新密码"></xa-input>
      <xa-input v-model="re_password" label="确认新密码" position="top" type="text" placeholder="请再次输入新密码"></xa-input>
    </div>
    <div style="padding:10px;">
      <xa-button @onclick="onBtnClick" ripple="rgba(255,255,255,0.3)" :radius="6" width="100%" class="xa-bg-red xa-txt-white" event="onclick">确认修改</xa-button>
    </div>
  </div>
</template>

<script>
import { setDocumentTitle } from 'utils'
import { modifyPassword } from 'service/member'

export default {
  data () {
    return {
      password: '',
      new_password: '',
      re_password: ''
    }
  },
  methods: {
    onBtnClick () {
      if (!this.password.trim()) {
        return this.$toast.show('请输入原密码', 3000)
      }
      if (!this.new_password.trim()) {
        return this.$toast.show('请输入新密码', 3000)
      }
      if (!this.re_password.trim()) {
        return this.$toast.show('请重新输入新密码', 3000)
      }
      if (this.new_password.trim().length < 6 || this.new_password.trim().length > 16) {
        return this.$toast.show('密码长度需在6-16个字符内', 3000)
      }
      if (this.new_password.trim() !== this.re_password.trim()) {
        return this.$toast.show('两次新密码输入不一致', 3000)
      }
      this.$toast.show('请稍后...')
      modifyPassword({password: this.password, new_password: this.new_password}).then(data => {
        this.$toast.show('密码修改成功！', 3000)
        this.$router.back()
      }).catch(() => {
        this.$toast.show('密码修改失败！', 3000)
      })
    }
  },
  created () {
    setDocumentTitle('修改密码')
  }
}
</script>



