import xaCopyright from './copyright'
import xaPopup from './popup'
import { cells as xaCells, cell as xaCell } from './cells'
import xaRipple from './ripple'
import xaCircular from './circular'
import xaTab from './tab'
import { button as xaButton, buttons as xaButtons } from './buttons'
import xaInfiniteScroll from './infiniteScroll'
import xaRefreshControl from './refreshControl'
import xaPicker from './picker'
import xaDatePicker from './datePicker'
import xaInput from './input'
import xaSelect from './select'
import xaTabBar from './tabBar'
import xaRadio from './radio'
import xaSearch from './search'
import xaDropdown from './dropdown'

import toast from './toast'

const components = {
  xaCopyright,
  xaPopup,
  xaCells,
  xaCell,
  xaRipple,
  xaCircular,
  xaTab,
  xaButton,
  xaButtons,
  xaInfiniteScroll,
  xaRefreshControl,
  xaPicker,
  xaDatePicker,
  xaInput,
  xaSelect,
  xaTabBar,
  xaRadio,
  xaSearch,
  xaDropdown
}

const install = (Vue, option) => {
  Object.keys(components).forEach((key) => {
    Vue.component(key, components[key])
  })
  Vue.prototype.$toast = toast()
}

export default {
  install
}



